import { useState, useEffect } from "react";
import { getData, postData } from "../Common/FuncAxios";
import { useForm, Controller } from "react-hook-form";
import "../../Css/UploadFile.scss";
import Select from "react-select";

const CreateCustommer = (props) => {
  const [IsLoading, SetIsLoading] = useState(true);
  const {
    register,
    reset,
    setValue,
    control,
    formState: { errors },
    handleSubmit,
  } = useForm({
    mode: "onChange",
  });

  const [listStatusType, setListStatusType] = useState([]);
  const [listCustomerGroup, setListCustomerGroup] = useState([]);
  const [listCustomerType, setListCustomerType] = useState([]);

  const [ListProvince, SetListProvince] = useState([]);
  const [ListDistrict, SetListDistrict] = useState([]);
  const [ListWard, SetListWard] = useState([]);

  const onSubmit = async (data, e) => {
    SetIsLoading(true);

    const post = await postData("Customer/CreateCustomer", {
      maKh: data.MaKH.toUpperCase(),
      tenKh: data.TenKH,
      maSoThue: data.MST,
      sdt: data.SDT,
      email: data.Email,
      trangThai: data.TrangThai,
      nhomKh: data.NhomKH,
      loaiKh: data.LoaiKH,
      address: {
        tenDiaDiem: "",
        maQuocGia: 1,
        maTinh: data.MaTinh.value,
        maHuyen: data.MaHuyen.value,
        maPhuong: data.MaPhuong.value,
        soNha: data.SoNha,
        diaChiDayDu: "",
        maGps: data.GPS,
        maLoaiDiaDiem: "1",
      },
    });
    console.log(data);
    if (post === 1) {
      props.getListUser(1);
      reset();
    }

    SetIsLoading(false);
  };

  useEffect(() => {
    if (props.listCusGroup && props.listCusType) {
      setListCustomerGroup(props.listCusGroup);
      setListCustomerType(props.listCusType);
    }
  }, [props.listCusGroup, props.listCusType]);

  useEffect(() => {
    SetIsLoading(true);

    SetListProvince([]);
    SetListDistrict([]);
    SetListWard([]);

    (async () => {
      const getListStatusType = await getData(
        `Common/GetListStatus?statusType=common`
      );
      setListStatusType(getListStatusType);
      const getlistProvince = await getData("address/GetListProvinces");

      if (getlistProvince && getlistProvince.length > 0) {
        var obj = [];
        obj.push({ value: "", label: "Chn Tnh" });
        getlistProvince.map((val) => {
          obj.push({
            value: val.maTinh,
            label: val.maTinh + " - " + val.tenTinh,
          });
        });

        SetListProvince(obj);
      }
    })();

    SetIsLoading(false);
  }, []);

  const HandleChangeProvince = (val) => {
    try {
      SetIsLoading(true);

      setValue("MaHuyen", { value: "", label: "Chn Huyn" });
      setValue("MaPhuong", { value: "", label: "Chn Phng" });
      if (val.value === undefined || val.value === "" || val === "") {
        SetListDistrict([]);
        SetListWard([]);
        SetIsLoading(false);
        return;
      }

      (async () => {
        const listDistrict = await getData(
          `address/GetListDistricts?ProvinceId=${val.value}`
        );

        if (listDistrict && listDistrict.length > 0) {
          var obj = [];
          obj.push({ value: "", label: "Chn Huyn" });
          listDistrict.map((val) => {
            obj.push({
              value: val.maHuyen,
              label: val.maHuyen + " - " + val.tenHuyen,
            });
          });

          SetListDistrict(obj);
          setValue("MaTinh", val);
        } else {
          SetListDistrict([]);
          SetListWard([]);
        }
      })();

      SetListDistrict([]);
      SetListWard([]);
      SetIsLoading(false);
    } catch (error) {}
  };

  const HandleOnchangeDistrict = (val) => {
    try {
      SetIsLoading(true);

      if (val.value === undefined || val.value === "") {
        SetListWard([]);
        return;
      }

      (async () => {
        const listWard = await getData(
          `address/GetListWards?DistrictId=${val.value}`
        );
        if (listWard && listWard.length > 0) {
          var obj = [];
          obj.push({ value: "", label: "Chn Phng" });
          listWard.map((val) => {
            obj.push({
              value: val.maPhuong,
              label: val.maPhuong + " - " + val.tenPhuong,
            });
          });

          setValue("MaHuyen", val);
          SetListWard(obj);
        } else {
          SetListWard([]);
        }
      })();

      SetIsLoading(false);
    } catch (error) {}
  };

  const handleResetClick = () => {
    reset();
    setValue("MaTinh", { value: "", label: "Chn Tnh" });
    setValue("MaHuyen", { value: "", label: "Chn Huyn" });
    setValue("MaPhuong", { value: "", label: "Chn Phng" });
  };

  return (
    <>
      <div className="card card-primary">
        <div className="card-header">
          <h3 className="card-title">Form Thm Mi Khch Hng</h3>
        </div>
        <div>{IsLoading === true && <div>Loading...</div>}</div>

        {IsLoading === false && (
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="card-body">
              <div className="form-group">
                <label htmlFor="MaKH">M khch hng</label>
                <input
                  autoComplete="false"
                  type="text"
                  className="form-control"
                  id="MaKH"
                  placeholder="Nhp m khch hng"
                  {...register("MaKH", {
                    required: "Khng c  trng",
                    maxLength: {
                      value: 8,
                      message: "Khng c vt qu 8 k t",
                    },
                    minLength: {
                      value: 8,
                      message: "Khng c t hn 8 k t",
                    },
                    pattern: {
                      value:
                        /^(?![_.])(?![_.])(?!.*[_.]{2})[a-zA-Z0-9]+(?<![_.])$/,
                      message: "Khng c cha k t c bit",
                    },
                  })}
                />
                {errors.MaKH && (
                  <span className="text-danger">{errors.MaKH.message}</span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="TenKH">Tn khch hng</label>
                <input
                  type="text"
                  className="form-control"
                  id="TenKH"
                  placeholder="Nhp tn khch hng"
                  {...register("TenKH", {
                    required: "Khng c  trng",
                    maxLength: {
                      value: 50,
                      message: "Khng c vt qu 50 k t",
                    },
                    minLength: {
                      value: 1,
                      message: "Khng c t hn 1 k t",
                    },
                    pattern: {
                      value:
                        /^(?![_.])(?![_.])(?!.*[_.]{2})[a-zA-Z0-9 aAbBcCdDeE fFgGhHiIjJkKlLmMnNoOpPqQrRsStTu UvVwWxXyYzZ]+(?<![_.])$/,
                      message: "Tn khch hng khng c cha k t c bit",
                    },
                  })}
                />
                {errors.TenKH && (
                  <span className="text-danger">{errors.TenKH.message}</span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="Email">a ch Email</label>
                <input
                  type="text "
                  className="form-control"
                  id="Email"
                  placeholder="Nhp a ch Email"
                  {...register("Email", {
                    required: "Khng c  trng",
                    maxLength: {
                      value: 100,
                      message: "Khng c vt qu 100 k t",
                    },
                    minLength: {
                      value: 3,
                      message: "Khng c t hn 3 k t",
                    },
                    pattern: {
                      value: /^([\w\.\-]+)@([\w\-]+)((\.(\w){2,3})+)$/,
                      message: "Khng phi Email",
                    },
                  })}
                />
                {errors.Email && (
                  <span className="text-danger">{errors.Email.message}</span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="MST">M s thu</label>
                <input
                  type="text "
                  className="form-control"
                  id="MST"
                  placeholder="Nhp m s thu"
                  {...register("MST", {
                    required: "Khng c  trng",
                    maxLength: {
                      value: 50,
                      message: "Khng c vt qu 50 k t",
                    },
                    minLength: {
                      value: 1,
                      message: "Khng c t hn 1 k t",
                    },
                    pattern: {
                      value: /^(?![_.])(?![_.])(?!.*[_.]{2})[0-9]+(?<![_.])$/,
                      message: "M s thu ch c cha k t l s",
                    },
                  })}
                />
                {errors.MST && (
                  <span className="text-danger">{errors.MST.message}</span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="SDT">S in thoi</label>
                <input
                  type="number"
                  className="form-control"
                  id="SDT"
                  placeholder="Nhp s in thoi"
                  {...register("SDT", {
                    required: "Khng c  trng",
                    maxLength: {
                      value: 50,
                      message: "Khng c vt qu 20 k t",
                    },
                    minLength: {
                      value: 10,
                      message: "Khng c t hn 10 k t",
                    },
                    pattern: {
                      value: /^(?![_.])(?![_.])(?!.*[_.]{2})[0-9]+(?<![_.])$/,
                      message: "S in thoi ch c cha k t l s",
                    },
                  })}
                />
                {errors.SDT && (
                  <span className="text-danger">{errors.SDT.message}</span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="GPS">M GPS</label>
                <input
                  type="text"
                  className="form-control"
                  id="GPS"
                  placeholder="Nhp m GPS"
                  {...register("GPS", {
                    required: "Khng c  trng",
                    maxLength: {
                      value: 50,
                      message: "Khng c vt qu 50 k t",
                    },
                    minLength: {
                      value: 1,
                      message: "Khng c t hn 1 k t",
                    },
                  })}
                />
                {errors.GPS && (
                  <span className="text-danger">{errors.GPS.message}</span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="NhomKH">Nhm khch hng</label>
                <select
                  className="form-control"
                  {...register("NhomKH", {
                    required: "Khng c  trng",
                  })}
                >
                  <option value="">Chn Nhm khch hng</option>
                  {listCustomerGroup &&
                    listCustomerGroup.map((val) => {
                      return (
                        <option value={val.maNhomKh} key={val.maNhomKh}>
                          {val.tenNhomKh}
                        </option>
                      );
                    })}
                </select>
                {errors.NhomKH && (
                  <span className="text-danger">{errors.NhomKH.message}</span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="LoaiKH">Phn Loi khch hng</label>
                <select
                  className="form-control"
                  {...register("LoaiKH", {
                    required: "Khng c  trng",
                  })}
                >
                  <option value="">Chn Phn Loi khch hng</option>
                  {listCustomerType &&
                    listCustomerType.map((val) => {
                      return (
                        <option value={val.maLoaiKh} key={val.maLoaiKh}>
                          {val.tenLoaiKh}
                        </option>
                      );
                    })}
                </select>
                {errors.LoaiKH && (
                  <span className="text-danger">{errors.LoaiKH.message}</span>
                )}
              </div>
              <div className="row">
                <div className="col-sm">
                  <div className="form-group">
                    <label htmlFor="Sonha">S nh</label>
                    <input
                      type="text"
                      className="form-control"
                      id="Sonha"
                      placeholder="Nhp s nh"
                      {...register("SoNha", {
                        required: "Khng c  trng",
                        maxLength: {
                          value: 100,
                          message: "Khng c vt qu 100 k t",
                        },
                        minLength: {
                          value: 1,
                          message: "Khng c t hn 1 k t",
                        },
                      })}
                    />
                    {errors.SoNha && (
                      <span className="text-danger">
                        {errors.SoNha.message}
                      </span>
                    )}
                  </div>
                </div>
                <div className="col-sm">
                  <div className="form-group">
                    <label htmlFor="Tinh">Tnh</label>
                    <Controller
                      name="MaTinh"
                      control={control}
                      render={({ field }) => (
                        <Select
                          {...field}
                          classNamePrefix={"form-control"}
                          value={field.value}
                          options={ListProvince}
                          onChange={(field) => HandleChangeProvince(field)}
                          defaultValue={{ value: "", label: "Chn Tnh" }}
                        />
                      )}
                      rules={{ required: "khng c  trng" }}
                    />
                    {errors.MaTinh && (
                      <span className="text-danger">
                        {errors.MaTinh.message}
                      </span>
                    )}
                  </div>
                </div>
                <div className="col-sm">
                  <div className="form-group">
                    <label htmlFor="SDT">Huyn</label>
                    <Controller
                      name="MaHuyen"
                      control={control}
                      render={({ field }) => (
                        <Select
                          {...field}
                          classNamePrefix={"form-control"}
                          value={field.value}
                          options={ListDistrict}
                          onChange={(field) => HandleOnchangeDistrict(field)}
                          defaultValue={{ value: "", label: "Chn Huyn" }}
                        />
                      )}
                      rules={{ required: "khng c  trng" }}
                    />
                    {errors.MaHuyen && (
                      <span className="text-danger">
                        {errors.MaHuyen.message}
                      </span>
                    )}
                  </div>
                </div>
                <div className="col-sm">
                  <div className="form-group">
                    <label htmlFor="SDT">Phng</label>
                    <Controller
                      name="MaPhuong"
                      control={control}
                      render={({ field }) => (
                        <Select
                          {...field}
                          classNamePrefix={"form-control"}
                          value={field.value}
                          options={ListWard}
                          defaultValue={{ value: "", label: "Chn Phng" }}
                        />
                      )}
                      rules={{ required: "khng c  trng" }}
                    />
                    {errors.MaPhuong && (
                      <span className="text-danger">
                        {errors.MaPhuong.message}
                      </span>
                    )}
                  </div>
                </div>
              </div>
              <div className="form-group">
                <label htmlFor="TrangThai">Trng thi</label>
                <select
                  className="form-control"
                  {...register("TrangThai", {
                    required: "Khng c  trng",
                  })}
                >
                  <option value="">Chn trng thi</option>
                  {listStatusType &&
                    listStatusType.map((val) => {
                      return (
                        <option value={val.maTrangThai} key={val.maTrangThai}>
                          {val.tenTrangThai}
                        </option>
                      );
                    })}
                </select>
                {errors.TrangThai && (
                  <span className="text-danger">
                    {errors.TrangThai.message}
                  </span>
                )}
              </div>
            </div>
            <div className="card-footer">
              <div>
                <button
                  type="button"
                  onClick={() => handleResetClick()}
                  className="btn btn-warning"
                >
                  Lm mi
                </button>
                <button
                  type="submit"
                  className="btn btn-primary"
                  style={{ float: "right" }}
                >
                  Thm mi
                </button>
              </div>
            </div>
          </form>
        )}
      </div>
    </>
  );
};

export default CreateCustommer;
