import { useState, useEffect } from "react";
import { getData, putData } from "../Common/FuncAxios";
import { useForm, Controller } from "react-hook-form";
import { ToastWarning } from "../Common/FuncToast";
import Select from "react-select";

const EditAddress = (props) => {
  const [IsLoading, SetIsLoading] = useState(true);
  const {
    register,
    setValue,
    control,
    formState: { errors },
    handleSubmit,
  } = useForm({
    mode: "onChange",
  });

  const [ListTypeAddress, SetListTypeAddress] = useState([]);
  const [ListProvince, SetListProvince] = useState([]);
  const [ListDistrict, SetListDistrict] = useState([]);
  const [ListWard, SetListWard] = useState([]);

  const Validate = {
    TenDiaDiem: {
      required: "Khng c  trng",
      maxLength: {
        value: 50,
        message: "Khng c vt qu 50 k t",
      },
      minLength: {
        value: 1,
        message: "Khng c t hn 1 k t",
      },
    },
    MaGPS: {
      required: "Khng c  trng",
      maxLength: {
        value: 50,
        message: "Khng c vt qu 50 k t",
      },
      minLength: {
        value: 1,
        message: "Khng c t hn 1 k t",
      },
    },
    SoNha: {
      required: "Khng c  trng",
      maxLength: {
        value: 100,
        message: "Khng c vt qu 100 k t",
      },
      minLength: {
        value: 1,
        message: "Khng c t hn 1 k t",
      },
    },
  };

  useEffect(() => {
    if (
      props &&
      props.selectIdClick &&
      Object.keys(props.selectIdClick).length > 0
    ) {
      setValue("MaDiaDiem", props.selectIdClick.maDiaDiem);
      setValue("TenDiaDiem", props.selectIdClick.tenDiaDiem);
      setValue("GPS", props.selectIdClick.maGps);
      setValue("SoNha", props.selectIdClick.soNha);

      LoadTypeAddress(props.selectIdClick.loaiDiaDiem);
      LoadProvince(props.selectIdClick.maTinh);
      LoadDistrict(props.selectIdClick.maTinh, props.selectIdClick.maHuyen);
      LoadWard(props.selectIdClick.maHuyen, props.selectIdClick.maPhuong);
    }
  }, [props.selectIdClick]);

  useEffect(() => {
    SetIsLoading(true);

    SetListProvince([]);
    SetListDistrict([]);
    SetListWard([]);

    SetIsLoading(false);
  }, []);

  const LoadTypeAddress = async (id) => {
    (async () => {
      const getListTypeAddress = await getData("address/GetListAddressType");

      if (getListTypeAddress && getListTypeAddress.length > 0) {
        let obj = [];
        obj.push({
          value: "",
          label: "Chn Loi a im",
        });
        getListTypeAddress.map((val) => {
          obj.push({
            value: val.maLoaiDiaDiem,
            label: val.maLoaiDiaDiem + " - " + val.tenLoaiDiaDiem,
          });
        });
        SetListTypeAddress(obj);

        setValue("MaLoaiDiaDiem", { ...obj.filter((x) => x.value === id)[0] });
      }
    })();
  };

  const LoadProvince = async (tinh) => {
    (async () => {
      const listProvince = await getData("address/GetListProvinces");
      if (listProvince && listProvince.length > 0) {
        var obj = [];
        obj.push({ value: "", label: "Chn Tnh" });
        listProvince.map((val) => {
          obj.push({
            value: val.maTinh,
            label: val.maTinh + " - " + val.tenTinh,
          });
        });
        SetListProvince(obj);
        setValue("MaTinh", { ...obj.filter((x) => x.value === tinh) }[0]);
      }
    })();
  };

  const LoadDistrict = async (tinh, huyen) => {
    (async () => {
      const listDistrict = await getData(
        `address/GetListDistricts?ProvinceId=${tinh}`
      );
      if (listDistrict && listDistrict.length > 0) {
        var obj = [];
        obj.push({ value: "", label: "Chn Huyn" });
        listDistrict.map((val) => {
          obj.push({
            value: val.maHuyen,
            label: val.maHuyen + " - " + val.tenHuyen,
          });
        });
        SetListDistrict(obj);

        setValue("MaHuyen", { ...obj.filter((x) => x.value === huyen) }[0]);
      } else {
        SetListDistrict([]);
      }
    })();
  };

  const LoadWard = async (huyen, phuong) => {
    (async () => {
      const listWard = await getData(
        `address/GetListWards?DistrictId=${huyen}`
      );

      if (listWard && listWard.length > 0) {
        var obj = [];
        obj.push({ value: "", label: "Chn Phng" });
        listWard.map((val) => {
          obj.push({
            value: val.maPhuong,
            label: val.maPhuong + " - " + val.tenPhuong,
          });
        });

        SetListWard(obj);
        setValue("MaPhuong", { ...obj.filter((x) => x.value === phuong) }[0]);
      } else {
        SetListWard([]);
      }
    })();
  };

  const HandleChangeProvince = (val) => {
    try {
      setValue("MaHuyen", { value: "", label: "Chn Huyn" });
      setValue("MaPhuong", { value: "", label: "Chn Phng" });
      SetListDistrict([]);
      SetListWard([]);
      if (val.value === undefined || val.value === "") {
        return;
      }

      SetIsLoading(true);
      setValue("MaTinh", val);
      LoadDistrict(val.value, "");
      SetIsLoading(false);
    } catch (error) {}
  };

  const HandleOnchangeDistrict = (val) => {
    try {
      if (val.value === undefined || val.value === "") {
        return;
      }
      SetIsLoading(true);
      LoadWard(val.value, "");
      setValue("MaHuyen", val);
      SetIsLoading(false);
    } catch (error) {}
  };

  const onSubmit = async (data) => {
    SetIsLoading(true);

    const Update = await putData(
      `Address/EditAddress?Id=${data.MaDiaDiem}`,
      {
        tenDiaDiem: data.TenDiaDiem,
        maQuocGia: 1,
        maTinh: data.MaTinh.value,
        maHuyen: data.MaHuyen.value,
        maPhuong: data.MaPhuong.value,
        soNha: data.SoNha,
        diaChiDayDu: "",
        maGps: data.GPS,
        maLoaiDiaDiem: data.MaLoaiDiaDiem.value,
      },
      {
        accept: "*/*",
        "Content-Type": "application/json",
      }
    );

    if (Update === 1) {
      props.getListAddress(1);
    }
    SetIsLoading(false);
  };

  return (
    <>
      <div className="card card-primary">
        <div className="card-header">
          <h3 className="card-title">Form Thm Mi a im</h3>
        </div>
        <div>{IsLoading === true && <div>Loading...</div>}</div>

        {IsLoading === false && (
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="card-body">
              <div className="row">
                <div className="col-sm">
                  <div className="form-group">
                    <label htmlFor="Tinh">Loi a im</label>
                    <Controller
                      name="MaLoaiDiaDiem"
                      control={control}
                      render={({ field }) => (
                        <Select
                          {...field}
                          classNamePrefix={"form-control"}
                          value={field.value}
                          options={ListTypeAddress}
                          defaultValue={{
                            value: "",
                            label: "Chn Loi a im",
                          }}
                        />
                      )}
                      rules={{ required: "khng c  trng" }}
                    />
                    {errors.MaLoaiDiaDiem && (
                      <span className="text-danger">
                        {errors.MaLoaiDiaDiem.message}
                      </span>
                    )}
                  </div>
                </div>
                <div className="col-sm">
                  <div className="form-group">
                    <label htmlFor="TenDiaDiem">Tn a im</label>
                    <input
                      type="text"
                      className="form-control"
                      id="TenDiaDiem"
                      placeholder="Nhp tn a im"
                      {...register("TenDiaDiem", Validate.TenDiaDiem)}
                    />
                    {errors.TenDiaDiem && (
                      <span className="text-danger">
                        {errors.TenDiaDiem.message}
                      </span>
                    )}
                  </div>
                </div>
              </div>
              <div className="form-group">
                <label htmlFor="GPS">M GPS</label>
                <input
                  type="text"
                  className="form-control"
                  id="GPS"
                  placeholder="Nhp m GPS"
                  {...register("GPS", Validate.MaGPS)}
                />
                {errors.GPS && (
                  <span className="text-danger">{errors.GPS.message}</span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="Sonha">S nh</label>
                <input
                  type="text"
                  className="form-control"
                  id="Sonha"
                  placeholder="Nhp s nh"
                  {...register("SoNha", Validate.SoNha)}
                />
                {errors.SoNha && (
                  <span className="text-danger">{errors.SoNha.message}</span>
                )}
              </div>
              <div className="row">
                <div className="col-sm">
                  <div className="form-group">
                    <label htmlFor="Tinh">Tnh</label>
                    <Controller
                      name="MaTinh"
                      control={control}
                      render={({ field }) => (
                        <Select
                          {...field}
                          classNamePrefix={"form-control"}
                          value={field.value}
                          options={ListProvince}
                          onChange={(field) => HandleChangeProvince(field)}
                          defaultValue={{ value: "", label: "Chn Tnh" }}
                        />
                      )}
                      rules={{ required: "khng c  trng" }}
                    />
                    {errors.MaTinh && (
                      <span className="text-danger">
                        {errors.MaTinh.message}
                      </span>
                    )}
                  </div>
                </div>
                <div className="col-sm">
                  <div className="form-group">
                    <label htmlFor="SDT">Huyn</label>
                    <Controller
                      name="MaHuyen"
                      control={control}
                      render={({ field }) => (
                        <Select
                          {...field}
                          classNamePrefix={"form-control"}
                          value={field.value}
                          options={ListDistrict}
                          onChange={(field) => HandleOnchangeDistrict(field)}
                          defaultValue={{ value: "", label: "Chn Huyn" }}
                        />
                      )}
                      rules={{ required: "khng c  trng" }}
                    />
                    {errors.MaHuyen && (
                      <span className="text-danger">
                        {errors.MaHuyen.message}
                      </span>
                    )}
                  </div>
                </div>
                <div className="col-sm">
                  <div className="form-group">
                    <label htmlFor="SDT">Phng</label>
                    <Controller
                      name="MaPhuong"
                      control={control}
                      render={({ field }) => (
                        <Select
                          {...field}
                          classNamePrefix={"form-control"}
                          value={field.value}
                          options={ListWard}
                          defaultValue={{ value: "", label: "Chn Phng" }}
                        />
                      )}
                      rules={{ required: "khng c  trng" }}
                    />
                    {errors.MaPhuong && (
                      <span className="text-danger">
                        {errors.MaPhuong.message}
                      </span>
                    )}
                  </div>
                </div>
              </div>
            </div>
            <div className="card-footer">
              <div>
                <button
                  type="submit"
                  className="btn btn-primary"
                  style={{ float: "right" }}
                >
                  Cp nht
                </button>
              </div>
            </div>
          </form>
        )}
      </div>
    </>
  );
};

export default EditAddress;
