import { useState, useEffect } from "react";
import { getData, postData } from "../Common/FuncAxios";
import { useForm, Controller } from "react-hook-form";
import DatePicker from "react-datepicker";

const AddContract = (props) => {
  const [IsLoading, SetIsLoading] = useState(true);
  const {
    register,
    reset,
    control,
    formState: { errors },
    handleSubmit,
  } = useForm({
    mode: "onChange",
  });

  const Validate = {
    MaHopDong: {
      required: "Khng c  trng",
      maxLength: {
        value: 10,
        message: "Khng c vt qu 10 k t",
      },
      pattern: {
        value: /^(?![_.])(?![_.])(?!.*[_.]{2})[a-zA-Z0-9]+(?<![_.])$/,
        message: "Khng c cha k t c bit",
      },
    },
    TenHopDong: {
      required: "Khng c  trng",
      maxLength: {
        value: 50,
        message: "Khng c vt qu 50 k t",
      },
      minLength: {
        value: 1,
        message: "Khng c t hn 1 k t",
      },
      pattern: {
        value:
          /^(?![_.])(?![_.])(?!.*[_.]{2})[a-zA-Z0-9 aAbBcCdDeE fFgGhHiIjJkKlLmMnNoOpPqQrRsStTu UvVwWxXyYzZ]+(?<![_.])$/,
        message: "Khng c cha k t c bit",
      },
    },
    MaKh: {
      required: "Khng c  trng",
      maxLength: {
        value: 8,
        message: "Khng c vt qu 8 k t",
      },
      minLength: {
        value: 8,
        message: "Khng c t hn 8 k t",
      },
      pattern: {
        value: /^(?![_.])(?![_.])(?!.*[_.]{2})[a-zA-Z0-9]+(?<![_.])$/,
        message: "Khng c cha k t c bit",
      },
    },
    PhanLoaiHopDong: {
      required: "Khng c  trng",
    },
    SoHopDongCha: {
      maxLength: {
        value: 10,
        message: "Khng c vt qu 10 k t",
      },
      pattern: {
        value: /^(?![_.])(?![_.])(?!.*[_.]{2})[a-zA-Z0-9]+(?<![_.])$/,
        message: "Khng c cha k t c bit",
      },
    },
    NgayBatDau: {
      required: "Khng c  trng",
      maxLength: {
        value: 10,
        message: "Khng c vt qu 10 k t",
      },
      minLength: {
        value: 10,
        message: "Khng c t hn 10 k t",
      },
      pettern: {
        value:
          /^(?:(?:31(\/|-|\.)(?:0?[13578]|1[02]))\1|(?:(?:29|30)(\/|-|\.)(?:0?[13-9]|1[0-2])\2))(?:(?:1[6-9]|[2-9]\d)?\d{2})$|^(?:29(\/|-|\.)0?2\3(?:(?:(?:1[6-9]|[2-9]\d)?(?:0[48]|[2468][048]|[13579][26])|(?:(?:16|[2468][048]|[3579][26])00))))$|^(?:0?[1-9]|1\d|2[0-8])(\/|-|\.)(?:(?:0?[1-9])|(?:1[0-2]))\4(?:(?:1[6-9]|[2-9]\d)?\d{2})$/,
        message: "Khng phi nh dng ngy",
      },
    },
    NgayKetThuc: {
      required: "Khng c  trng",
      maxLength: {
        value: 10,
        message: "Khng c vt qu 10 k t",
      },
      minLength: {
        value: 10,
        message: "Khng c t hn 10 k t",
      },
      pettern: {
        value:
          /^(?:(?:31(\/|-|\.)(?:0?[13578]|1[02]))\1|(?:(?:29|30)(\/|-|\.)(?:0?[13-9]|1[0-2])\2))(?:(?:1[6-9]|[2-9]\d)?\d{2})$|^(?:29(\/|-|\.)0?2\3(?:(?:(?:1[6-9]|[2-9]\d)?(?:0[48]|[2468][048]|[13579][26])|(?:(?:16|[2468][048]|[3579][26])00))))$|^(?:0?[1-9]|1\d|2[0-8])(\/|-|\.)(?:(?:0?[1-9])|(?:1[0-2]))\4(?:(?:1[6-9]|[2-9]\d)?\d{2})$/,
        message: "Khng phi nh dng ngy",
      },
    },
    PTVC: {
      required: "Khng c  trng",
    },
    TrangThai: {
      required: "Khng c  trng",
    },
  };

  const [listContractType, setListContractType] = useState([]);
  const [listTransportType, setListTransportType] = useState([]);
  const [listStatusType, setListStatusType] = useState([]);

  useEffect(() => {
    if (props && props.listContractType) {
      setListContractType(props.listContractType);
    }
  }, [props, props.listContractType]);

  useEffect(() => {
    SetIsLoading(true);

    (async () => {
      const getListTransportType = await getData("Common/GetListTransportType");
      setListTransportType(getListTransportType);

      const getListStatusType = await getData(
        `Common/GetListStatus?statusType=common`
      );
      setListStatusType(getListStatusType);
    })();

    SetIsLoading(false);
  }, []);

  const handleResetClick = () => {
    reset();
  };

  const onSubmit = async (data) => {
    SetIsLoading(true);

    var create = await postData(
      "Contract/CreateContract",
      {
        maHopDong: data.MaHopDong,
        soHopDongCha: data.SoHopDongCha,
        tenHienThi: data.TenHopDong,
        maKh: data.MaKh,
        maPtvc: data.PTVC,
        phanLoaiHopDong: data.PhanLoaiHopDong,
        thoiGianBatDau: data.NgayBatDau,
        thoiGianKetThuc: data.NgayKetThuc,
        ghiChu: data.GhiChu,
        phuPhi: null,
        trangThai: data.TrangThai,
        file: data.FileContact[0],
      },
      {
        headers: { "Content-Type": "multipart/form-data" },
      }
    );

    if (create === 1) {
      props.getListContract(1);
      reset();
    }

    SetIsLoading(false);
  };

  return (
    <>
      <div className="card card-primary">
        <div className="card-header">
          <h3 className="card-title">Form Thm Mi Hp ng</h3>
        </div>
        <div>{IsLoading === true && <div>Loading...</div>}</div>

        {IsLoading === false && (
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="card-body">
              <div className="form-group">
                <label htmlFor="MaHopDong">M Hp ng</label>
                <input
                  autoComplete="false"
                  type="text"
                  className="form-control"
                  id="MaHopDong"
                  placeholder="Nhp m hp ng"
                  {...register("MaHopDong", Validate.MaHopDong)}
                />
                {errors.MaHopDong && (
                  <span className="text-danger">
                    {errors.MaHopDong.message}
                  </span>
                )}
              </div>

              <div className="form-group">
                <label htmlFor="TenHopDong">Tn Hp ng</label>
                <input
                  type="text"
                  className="form-control"
                  id="TenHopDong"
                  placeholder="Nhp tn khch hng"
                  {...register("TenHopDong", Validate.TenHopDong)}
                />
                {errors.TenHopDong && (
                  <span className="text-danger">
                    {errors.TenHopDong.message}
                  </span>
                )}
              </div>

              <div className="form-group">
                <label htmlFor="MaKh">M Khch Hng</label>
                <input
                  type="text "
                  className="form-control"
                  id="MaKh"
                  placeholder="Nhp m khch hng"
                  {...register("MaKh", Validate.MaKh)}
                />
                {errors.MaKh && (
                  <span className="text-danger">{errors.MaKh.message}</span>
                )}
              </div>

              <div className="form-group">
                <label htmlFor="SoHopDongCha">S hp ng cha</label>
                <input
                  autoComplete="false"
                  type="text"
                  className="form-control"
                  id="SoHopDongCha"
                  placeholder="Nhp S hp ng cha (b trng nu l hp ng chnh)"
                  {...register("SoHopDongCha", Validate.SoHopDongCha)}
                />
                {errors.SoHopDongCha && (
                  <span className="text-danger">
                    {errors.SoHopDongCha.message}
                  </span>
                )}
              </div>

              <div className="form-group">
                <label htmlFor="PhanLoaiHopDong">Phn Loi Hp ng</label>
                <select
                  className="form-control"
                  {...register("PhanLoaiHopDong", Validate.PhanLoaiHopDong)}
                >
                  <option value="">Chn phn loi H</option>
                  {listContractType &&
                    listContractType.map((val) => {
                      return (
                        <option
                          value={val.maLoaiHopDong}
                          key={val.maLoaiHopDong}
                        >
                          {val.tenLoaiHopDong}
                        </option>
                      );
                    })}
                </select>
                {errors.PhanLoaiHopDong && (
                  <span className="text-danger">
                    {errors.PhanLoaiHopDong.message}
                  </span>
                )}
              </div>

              <div className="form-group">
                <label htmlFor="PTVC">Phng thc vn chuyn</label>
                <select
                  className="form-control"
                  {...register("PTVC", Validate.PTVC)}
                >
                  <option value="">Chn phng thc vn chuyn</option>
                  {listTransportType &&
                    listTransportType.map((val) => {
                      return (
                        <option value={val.maPtvc} key={val.maPtvc}>
                          {val.tenPtvc}
                        </option>
                      );
                    })}
                </select>
                {errors.PTVC && (
                  <span className="text-danger">{errors.PTVC.message}</span>
                )}
              </div>

              <div className="form-group">
                <label htmlFor="NgayBatDau">Ngy bt u</label>
                <div className="input-group ">
                  <Controller
                    control={control}
                    name="NgayBatDau"
                    render={({ field }) => (
                      <DatePicker
                        className="form-control"
                        dateFormat="dd/MM/yyyy"
                        placeholderText="Chn ngy bt u"
                        onChange={(date) => field.onChange(date)}
                        selected={field.value}
                      />
                    )}
                    rules={Validate.NgayBatDau}
                  />
                  {errors.NgayBatDau && (
                    <span className="text-danger">
                      {errors.NgayBatDau.message}
                    </span>
                  )}
                </div>
              </div>

              <div className="form-group">
                <label htmlFor="NgayKetThuc">Ngy kt thc</label>
                <div className="input-group ">
                  <Controller
                    control={control}
                    name="NgayKetThuc"
                    render={({ field }) => (
                      <DatePicker
                        className="form-control"
                        dateFormat="dd/MM/yyyy"
                        placeholderText="Chn ngy bt u"
                        onChange={(date) => field.onChange(date)}
                        selected={field.value}
                      />
                    )}
                    rules={Validate.NgayKetThuc}
                  />
                  {errors.NgayKetThuc && (
                    <span className="text-danger">
                      {errors.NgayKetThuc.message}
                    </span>
                  )}
                </div>
              </div>

              <div className="form-group">
                <label htmlFor="GhiChu">Ghi Ch</label>
                <input
                  type="text"
                  className="form-control"
                  id="GhiChu"
                  placeholder="Nhp ghi ch"
                  {...register("GhiChu")}
                />
                {errors.GhiChu && (
                  <span className="text-danger">{errors.GhiChu.message}</span>
                )}
              </div>

              <div className="form-group">
                <label htmlFor="FileContact">Ti ln tp Hp ng</label>
                <input
                  type="file"
                  className="form-control-file"
                  {...register("FileContact", Validate.FileContact)}
                />
                {errors.FileContact && (
                  <span className="text-danger">
                    {errors.FileContact.message}
                  </span>
                )}
              </div>

              <div className="form-group">
                <label htmlFor="TrangThai">Trng thi</label>
                <select
                  className="form-control"
                  {...register("TrangThai", Validate.TrangThai)}
                >
                  <option value="">Chn trng thi</option>
                  {listStatusType &&
                    listStatusType.map((val) => {
                      return (
                        <option value={val.maTrangThai} key={val.maTrangThai}>
                          {val.tenTrangThai}
                        </option>
                      );
                    })}
                </select>
                {errors.TrangThai && (
                  <span className="text-danger">
                    {errors.TrangThai.message}
                  </span>
                )}
              </div>
            </div>
            <div className="card-footer">
              <div>
                <button
                  type="button"
                  onClick={() => handleResetClick()}
                  className="btn btn-warning"
                >
                  Lm mi
                </button>
                <button
                  type="submit"
                  className="btn btn-primary"
                  style={{ float: "right" }}
                >
                  Thm mi
                </button>
              </div>
            </div>
          </form>
        )}
      </div>
    </>
  );
};

export default AddContract;
