import { useState, useEffect } from "react";
import { getData, postData } from "../Common/FuncAxios";
import { useForm, Controller } from "react-hook-form";
import Select from "react-select";

const AddRoad = (props) => {
  const [IsLoading, SetIsLoading] = useState(true);
  const {
    register,
    reset,
    setValue,
    control,
    formState: { errors },
    handleSubmit,
  } = useForm({
    mode: "onChange",
  });

  const [listAddress, SetListAddress] = useState([]);

  const Validate = {
    MaCungDuong: {
      required: "Khng c  trng",
      maxLength: {
        value: 10,
        message: "Khng c vt qu 10 k t",
      },
      minLength: {
        value: 10,
        message: "Khng c t hn 10 k t",
      },
      pattern: {
        value: /^(?![_.])(?![_.])(?!.*[_.]{2})[a-zA-Z0-9]+(?<![_.])$/,
        message: "Khng c cha k t c bit",
      },
    },
    TenCungDuong: {
      required: "Khng c  trng",
      maxLength: {
        value: 100,
        message: "Khng c vt qu 100 k t",
      },
      minLength: {
        value: 1,
        message: "Khng c t hn 1 k t",
      },
      pattern: {
        value:
          /^(?![_.])(?![_.])(?!.*[_.]{2})[a-zA-Z0-9 aAbBcCdDeE fFgGhHiIjJkKlLmMnNoOpPqQrRsStTu UvVwWxXyYzZ]+(?<![_.])$/,
        message: "Tn khch hng khng c cha k t c bit",
      },
    },
    MaHopDong: {
      required: "Khng c  trng",
      maxLength: {
        value: 10,
        message: "Khng c vt qu 10 k t",
      },
      pattern: {
        value: /^(?![_.])(?![_.])(?!.*[_.]{2})[a-zA-Z0-9]+(?<![_.])$/,
        message: "Khng c cha k t c bit",
      },
    },

    SoKM: {
      required: "Khng c  trng",
      maxLength: {
        value: 100,
        message: "Khng c vt qu 100 k t",
      },
      minLength: {
        value: 1,
        message: "Khng c t hn 1 k t",
      },
      pattern: {
        value:
          /^(?:0\.(?:0[0-9]|[0-9]\d?)|[0-9]\d*(?:\.\d{1,2})?)(?:e[+-]?\d+)?$/,
        message: "Phi l s",
      },
    },
  };

  useEffect(() => {
    SetIsLoading(true);

    (async () => {
      const getlistAddress = await getData("address/GetListAddressSelect");

      if (getlistAddress && getlistAddress.length > 0) {
        var obj = [];
        obj.push({ value: "", label: "-- Chn --" });
        getlistAddress.map((val) => {
          obj.push({
            value: val.maDiaDiem,
            label:
              val.maDiaDiem + " - " + val.tenDiaDiem + " --- " + val.diaChi,
          });
        });

        SetListAddress(obj);
      }
    })();

    SetIsLoading(false);
  }, []);

  const handleResetClick = () => {
    reset();
    setValue("DiemDau", { value: "", label: "-- Chn --" });
    setValue("DiemCuoi", { value: "", label: "-- Chn --" });
    setValue("DiemLayRong", { value: "", label: "-- Chn --" });
  };

  const onSubmit = async (data, e) => {
    SetIsLoading(true);
    const post = await postData("Road/CreateRoad", {
      maCungDuong: data.MaCungDuong,
      tenCungDuong: data.TenCungDuong,
      maHopDong: data.MaHopDong,
      km: data.SoKM,
      diemDau: data.DiemDau.value,
      diemCuoi: data.DiemCuoi.value,
      diemLayRong: data.DiemLayRong.value,
      ghiChu: data.GhiChu,
    });

    if (post === 1) {
      props.getListRoad(1);
      reset();
    }

    SetIsLoading(false);
  };

  return (
    <>
      <div className="card card-primary">
        <div className="card-header">
          <h3 className="card-title">Form Thm Mi Cung ng</h3>
        </div>
        <div>{IsLoading === true && <div>Loading...</div>}</div>

        {IsLoading === false && (
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="card-body">
              <div className="form-group">
                <label htmlFor="MaCungDuong">M cung ng</label>
                <input
                  autoComplete="false"
                  type="text"
                  className="form-control"
                  id="MaCungDuong"
                  placeholder="Nhp m cung ng"
                  {...register("MaCungDuong", Validate.MaCungDuong)}
                />
                {errors.MaCungDuong && (
                  <span className="text-danger">
                    {errors.MaCungDuong.message}
                  </span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="TenCungDuong">Tn cung ng</label>
                <input
                  type="text"
                  className="form-control"
                  id="TenCungDuong"
                  placeholder="Nhp tn khch hng"
                  {...register("TenCungDuong", Validate.TenCungDuong)}
                />
                {errors.TenCungDuong && (
                  <span className="text-danger">
                    {errors.TenCungDuong.message}
                  </span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="MaHopDong">M hp ng</label>
                <input
                  type="text "
                  className="form-control"
                  id="MaHopDong"
                  placeholder="Nhp m hp ng"
                  {...register("MaHopDong", Validate.MaHopDong)}
                />
                {errors.MaHopDong && (
                  <span className="text-danger">
                    {errors.MaHopDong.message}
                  </span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="SoKM">S KM</label>
                <input
                  type="text "
                  className="form-control"
                  id="SoKM"
                  placeholder="Nhp S KM"
                  {...register("SoKM", Validate.SoKM)}
                />
                {errors.SoKM && (
                  <span className="text-danger">{errors.SoKM.message}</span>
                )}
              </div>

              <div className="form-group">
                <label htmlFor="DiemLayRong">im ly rng</label>
                <Controller
                  name="DiemLayRong"
                  control={control}
                  render={({ field }) => (
                    <Select
                      {...field}
                      classNamePrefix={"form-control"}
                      value={field.value}
                      options={listAddress}
                      defaultValue={{ value: "", label: "-- Chn --" }}
                    />
                  )}
                  rules={{ required: "khng c  trng" }}
                />
                {errors.DiemLayRong && (
                  <span className="text-danger">
                    {errors.DiemLayRong.message}
                  </span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="DiemDau">im u</label>
                <Controller
                  name="DiemDau"
                  control={control}
                  render={({ field }) => (
                    <Select
                      {...field}
                      classNamePrefix={"form-control"}
                      value={field.value}
                      options={listAddress}
                      defaultValue={{ value: "", label: "-- Chn --" }}
                    />
                  )}
                  rules={{ required: "khng c  trng" }}
                />
                {errors.DiemDau && (
                  <span className="text-danger">{errors.DiemDau.message}</span>
                )}
              </div>

              <div className="form-group">
                <label htmlFor="DiemCuoi">im cui</label>
                <Controller
                  name="DiemCuoi"
                  control={control}
                  render={({ field }) => (
                    <Select
                      {...field}
                      classNamePrefix={"form-control"}
                      value={field.value}
                      options={listAddress}
                      defaultValue={{ value: "", label: "-- Chn --" }}
                    />
                  )}
                  rules={{ required: "khng c  trng" }}
                />
                {errors.DiemCuoi && (
                  <span className="text-danger">{errors.DiemCuoi.message}</span>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="GhiChu">Ghi Ch</label>
                <input
                  type="text"
                  className="form-control"
                  id="GhiChu"
                  placeholder="Nhp ghi ch"
                  {...register("GhiChu")}
                />
                {errors.GhiChu && (
                  <span className="text-danger">{errors.GhiChu.message}</span>
                )}
              </div>
            </div>
            <div className="card-footer">
              <div>
                <button
                  type="button"
                  onClick={() => handleResetClick()}
                  className="btn btn-warning"
                >
                  Lm mi
                </button>
                <button
                  type="submit"
                  className="btn btn-primary"
                  style={{ float: "right" }}
                >
                  Thm mi
                </button>
              </div>
            </div>
          </form>
        )}
      </div>
    </>
  );
};

export default AddRoad;
